/* Webfont: Yekan */
@font-face {
  font-family: "Yekan";
  src: url("../public/fonts/Yekan.eot"); /* IE9 Compat Modes */
  src: url("../public/fonts/Yekan.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("../public/fonts/Yekan.woff2") format("woff2"),
    /* Modern Browsers */ url("../public/fonts/Yekan.woff") format("woff"),
    /* Modern Browsers */ url("../public/fonts/Yekan.otf") format("opentype"),
    /* Open Type Font */ url("../public/fonts/Yekan.ttf") format("truetype"); /* Safari, Android, iOS */
  font-weight: normal;
  font-style: italic;
  text-rendering: optimizeLegibility;
  font-display: auto;
}
:root {
  --background: #0e0026;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Yekan", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--background);
  color: #fff;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
