.grid-item {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  position: relative;
}
.grid-item img {
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}
.grid-item .front {
  position: absolute;
  transform: rotateY(90deg);
  transition: 0.2s all ease-in;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}
.grid-item .back {
  transition: all ease-in 0.2s;
}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0;
}
